.numbers {
  margin-bottom: 10px;
  button {
    cursor: pointer;
    &:active {
      background-color: #000000;
      color: #ffffff;
    }
    border: 2px solid #000000;
    font-size: 16px;
    font-weight: 500;
    padding: 12px 30px;
    background-color: transparent;
  }
}

.numbers:not(:nth-child(3n)) {
  margin-right: 10px;
}
